    padding: 10px 20px;
    border-radius: 4px;
    margin-top: 25px;
    cursor: pointer;
}

.sendButton span {
  	cursor: pointer;
  	display: inline-block;
  	position: relative;
  	transition: 0.5s;
}

.sendButton span:after {
  	content: '\00bb';
  	position: absolute;
  	opacity: 0;
  	top: 0;
  	right: -15px;
  	transition: 0.5s;
}

.sendButton:hover span {
  	padding-right: 15px;
}

.sendButton:hover span:after {
  	opacity: 1;
  	right: 0;
}


